ul {
  list-style-type: none; }

.header {
  font-family: sans-serif; }

.placeholder {
  font-style: italic; }

.show-card,
.hide-card {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.hide-card {
  display: none; }

.show-card {
  display: block; }

.pre-game,
.welcome {
  text-align: center;
  padding: 10px; }

.welcome {
  font-size: 20px; }

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  background-color: #f5f5f5;
  padding: 10px;
  margin: 10px auto;
  max-width: 700px; }

.grid-item, .grid-item-hide, .grid-item-show {
  padding: 20px;
  font-size: 30px;
  height: 70px;
  text-align: center;
  position: relative;
  border-radius: 10%;
  margin: 10px;
  cursor: pointer;
  -webkit-box-shadow: 1px 2px 8px #888888;
          box-shadow: 1px 2px 8px #888888; }

.grid-item-hide {
  background-color: #003366;
  background: #020024;
  background: -webkit-gradient(linear, left bottom, left top, from(#020024), color-stop(35%, #090979), to(#00d4ff));
  background: -webkit-linear-gradient(bottom, #020024 0%, #090979 35%, #00d4ff 100%);
  background: -o-linear-gradient(bottom, #020024 0%, #090979 35%, #00d4ff 100%);
  background: linear-gradient(0deg, #020024 0%, #090979 35%, #00d4ff 100%); }

.grid-item-show {
  border: 1px solid #ccc;
  background-color: #fff; }



